import { Form } from "./admin/columns/form";
import { EventConstants } from "./constants";
import { initAdminColumnsGlobalBootstrap } from "./helpers/admin-columns";
// @ts-ignore
import $ from 'jquery';
import ColumnConfigurator from "./admin/columns/column-configurator";
import Modal from "./modules/modal";
import Feedback from "./admin/columns/feedback";
import InfoScreenOption from "./admin/columns/screen-options";
initAdminColumnsGlobalBootstrap();
new ColumnConfigurator();
document.addEventListener('DOMContentLoaded', () => {
    new SaveButtons();
    let formElement = document.querySelector('#listscreen_settings');
    if (formElement) {
        AdminColumns.Form = new Form(formElement, AdminColumns.events);
    }
    let proModal = document.querySelector('#ac-modal-pro');
    if (proModal) {
        AdminColumns.Modals.register(new Modal(proModal), 'pro');
    }
    let select = document.querySelector('#ac_list_screen');
    if (select) {
        select.addEventListener('change', () => {
            document.querySelectorAll('.view-link').forEach(link => link.style.display = 'none');
            select.closest('form').submit();
            select.disabled = true;
            select.nextElementSibling.style.display = 'inline-block';
        });
    }
    let feedback = document.querySelector('#direct-feedback');
    if (feedback) {
        new Feedback(feedback);
    }
    // Column
    document.querySelectorAll('[data-ac-screen-option="show_column_id"] input').forEach(el => new InfoScreenOption(el, 'show-column-id', document.querySelector('.ac-boxes')));
    document.querySelectorAll('[data-ac-screen-option="show_column_type"] input').forEach(el => new InfoScreenOption(el, 'show-column-type', document.querySelector('.ac-boxes')));
    document.querySelectorAll('[data-ac-screen-option="show_list_screen_id"] input').forEach(el => new InfoScreenOption(el, 'show-list-screen-id', document.querySelector('.ac-admin')));
    document.querySelectorAll('[data-ac-screen-option="show_list_screen_type"] input').forEach(el => new InfoScreenOption(el, 'show-list-screen-type', document.querySelector('.ac-admin')));
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, (form) => {
    document.querySelectorAll('.add_column').forEach(el => {
        el.addEventListener('click', () => form.createNewColumn());
    });
    document.querySelectorAll('a[data-clear-columns]').forEach(el => {
        el.addEventListener('click', () => form.resetColumns());
    });
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVING, () => {
    document.querySelector('#cpac .ac-admin').classList.add('saving');
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVED, () => {
    document.querySelector('#cpac .ac-admin').classList.remove('saving');
    document.querySelector('#cpac .ac-admin').classList.add('stored');
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, (form) => {
    let $form = $(form.getElement());
    if ($form.hasClass('ui-sortable')) {
        $form.sortable('refresh');
    }
    else {
        $form.sortable({
            items: '.ac-column',
            handle: '.column_sort'
        });
    }
});
AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.LOADED, (form) => {
    document.querySelectorAll('a[data-clear-columns]').forEach(el => {
        el.addEventListener('click', () => form.resetColumns());
    });
});
class SaveButtons {
    constructor() {
        this.elements = document.querySelectorAll('.sidebox a.submit, .column-footer a.submit');
        this.init();
    }
    init() {
        AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.READY, (form) => {
            this.elements.forEach(el => {
                el.addEventListener('click', e => {
                    e.preventDefault();
                    this.disable();
                    form.submitForm();
                });
            });
        });
        AdminColumns.events.addListener(EventConstants.SETTINGS.FORM.SAVED, () => this.enable());
    }
    disable() {
        this.elements.forEach(el => el.setAttribute('disabled', 'disabled'));
    }
    enable() {
        this.elements.forEach(el => el.removeAttribute('disabled'));
    }
}
